/*radio  checked 框*/
.d-check{position: relative;vertical-align: middle; display: inline-block; padding-left: 24px; height: 15px; line-height: 15px;}
.d-check input,
.d-check input[type=checkbox] ~ i,
.d-check input[type=radio] ~ i{position: absolute;left:0;top:0; width: 15px; height: 15px; cursor: pointer;}
.d-check i:before{ 
	content: "";
	position: absolute; background:transparent; width: 7px; height: 4px; 
	top:1px;
	left:2px;
	border-left: 2px solid #f2f2f2; border-bottom: 2px solid #f2f2f2; 
	-webkit-transform: rotate(-45deg) scale(0); 
	        transform: rotate(-45deg) scale(0);
	-webkit-transition: all .2s;
	transition: all .2s
}
.d-check input{z-index: 1; opacity: 0; width: 100%;}
.d-check label{display: inline-block; color: #666; line-height: 14px;height: 14px;}
.d-check input[type=checkbox] ~ i,
.d-check input[type=radio] ~ i{ z-index: 0; -webkit-transition:all .2s; transition:all .2s; background: #fff;border:1px solid #ccc; -webkit-box-sizing: border-box; box-sizing: border-box; }
.d-check input:hover ~ i{ border-color:#ff6666; }
.d-check input:checked ~ i{ background:#ff6666; border-color: #ff6666  }
.d-check input:checked ~ i:before{
	border-color: #fff;
	-webkit-transform: rotate(-45deg) scale(1);
	        transform: rotate(-45deg) scale(1);
}
.d-check input[type=radio] ~ i{ border-radius: 50%; }
.d-check input[type=radio] ~ i:before{ width: 5px; height: 5px; right: 0; top:0; left:0; bottom:0; margin:auto; background: #fff;border-radius: 4px;border:none; }
.d-check input[type=checkbox] ~ label,
.d-check input[type=radio] ~ label{ color: #ff6666; }
input[disabled]{cursor: not-allowed;}
.d-check input[disabled] ~i {background: #f1efef; color: #999; cursor: not-allowed;}
.d-check input[disabled]:hover ~ i{ border-color:#ccc; }
